import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT-Test.sol/RuneToken.json';
import placeholder from '../imgs/placeholder.png';


function Sale() {
  const contractAddress = process.env.CONTRACT_ADDRESS;
  const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
  const signer = provider.getSigner();
  const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
	const [price, setPrice] = useState(0);

	useEffect(() => {
		async function getPrice() {
			const result = await contract.status();
      if (result == 2) setPrice(ethers.utils.parseEther("0.06"));
      else setPrice(ethers.utils.parseEther("0.05"));
		}
    if(price == 0) getPrice();
	}, []);

	async function mintToken() {
    document.getElementById('mintBtn').setAttribute('disabled', 'true');
		const minted = await contract.mintRune({
			value: price,
		}).then(res => res.wait())
			.catch(err => alert("Sorry, we are unable to mint your token at this time.\n" + err.message));
		if (minted) {
			window.location.reload();
		}
	}

  return (
    <>
      <h2 className="uk-heading-medium uk-text-center">Mint Your Rune NFT!</h2>
      <div className="uk-margin-medium">
          <div className="uk-flex uk-flex-center uk-text-center" uk-grid="true">
            <div className="uk-card uk-card-default uk-card-body">
              <div className='uk-flex uk-flex-column uk-flex-center uk-margin-auto'>
                <div>
                  <img data-src={placeholder} width="128" height="128" alt="Mint a Rune NFT" uk-img="true"/>
                </div>
                <button id="mintBtn" className="uk-button uk-button-primary" onClick={mintToken}>Mint</button>
              </div>
            </div>
          </div>
      </div>
    </>
  )
}

export default Sale;