import { useEffect, useState } from 'react';
import { ethers } from 'ethers';
import RunesNFT from '../artifacts/contracts/RunesNFT-Test.sol/RuneToken.json';


function Whitelist() {
    const contractAddress = process.env.CONTRACT_ADDRESS;
    const provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    const signer = provider.getSigner();
    const contract = new ethers.Contract(contractAddress, RunesNFT.abi, signer);
    const [totalMinted, setTotalMinted] = useState(0);
    const [mintsAllowed, setMintsAllowed] = useState(0);

    useEffect(() => {
      getCount();
    }, [totalMinted]);
  
    async function getCount() {
        await contract.totalSupply()
            .then(a => setTotalMinted(parseInt(a)));
    }

    useEffect(() => {
      numAvailable();
    }, [mintsAllowed]);
    
    async function numAvailable() {
        let a = await signer.getAddress();
        await contract.isWhiteListed(a)
            .then(a => setMintsAllowed(a))
            .catch(e => console.log(e));
    }

    async function joinList() {
        document.getElementById('joinButton').setAttribute("disabled", true);
        document.getElementById('joinButton').innerHTML = "Joining List...";
        if (mintsAllowed == 0) {
            await contract.joinWhiteList({value: ethers.utils.parseEther("0")})
                .catch(e => alert(e.message));
        }
        window.location.reload();
    }

    if (mintsAllowed == 0) {
        return (
            <p className='uk-text-center uk-padding-large uk-padding-remove-horizontal uk-padding-remove-bottom'>
                <button id="joinButton" className="uk-button uk-button-large uk-button-primary" onClick={joinList}>Join the Whitelist</button>
            </p>
        );
    } else {
        return  (
            <p className='uk-text-center uk-padding-large uk-padding-remove-horizontal uk-padding-remove-bottom'>
                <button className="uk-button uk-button-large uk-button-secondary" disabled={true}>Your Whitelisted!</button>
            </p>
        );
    }
}

export default Whitelist;